<!--  -->
<template>
	<div id="WoDeGuanZhu">
		<v-headr :myname="myname"></v-headr>
		<div class="my-concern-container">
			<!--  -->
	        <!-- <ul class="my-concern-warp aui-card-list" v-for="item in datas">
	            <li class="my-concern-list aui-panel-cell">
	                <div class="my-concern-pic "></div>
	                <div class="aui-panel-cell-bd">
	                    <h4>{{item.src}}</h4>
	                </div>
	                <div class="right_tex my-concerned"> </div>
	            </li>
	        </ul> -->

	        <ul class="my-concern-warp aui-card-list">
	            <li  v-for="item in datas"
	            class="my-concern-list aui-panel-cell">
	                <div class="my-concern-pic "></div>
	                <div class="aui-panel-cell-bd">
	                    <h4>{{item.SourceName}}</h4>
	                </div>
	                <div class="right_tex my-concerned"> </div>
	            </li> 
	        </ul>
	    </div>




	    <div class="add-more-con">
	        <a class="add-more" href="javascript:;">
	            <span class="add-pic"></span>
	            <span class="add-btu"></span> 
	        </a>
	    </div>
	</div>
</template>

<script type="text/javascript">
	const dataSrc = JSON.parse(sessionStorage.datas) // 
	import Headr from '../../components/header.vue' 
	export default {
		name:'WoDeGuanZhu',
		data(){
			return {
				msg:'',
				myname:{
					name:'',
					IsSubPage:true,
				},
				datas:null 
			}
		},
		components:{
			'v-headr':Headr,
		},
		methods:{
			childClick(){
				this.$emit('childByValue',this.IsSubPage)
			},
			init_gz(){
				var scThis = this; 
				var user_id = sessionStorage.userId; 
				this.axios.get('/api/flxoa/Follow?tokenid='+user_id+'&Type=1').then(function(response){ 
					console.log('-----------------------------------------')
					console.dir(response.data[0].data);
	                scThis.datas = response.data[0].data;
	            });  
			},
			islogin:function(){
                var user_id = sessionStorage.userId;
                if(user_id == null){ 
                    this.$messagebox({ 
                 title: '', 
                 message: '', 
                 showCancelButton: true, 
                 confirmButtonText:"", 
                 cancelButtonText:"" 
                }).then(action => { 
                 if(action == 'confirm'){ 
                  console.log('')
                 }else{ 
                        window.location.href = '/Login';
                 } 
               }) 
                    return;


                }

                this.childClick()
				this.init_gz();
                this.tabIndex = 2;
            }

		},
		mounted(){
			this.islogin()
			// this.init_gz();
			// this.tabIndex = 2;
			//alert(22)
		}

	}
</script>


