	import Headr from '../../components/header.vue'
	export default {
		name:'QiYeZhiGong',
		data(){
			return {
				myname:{
					name:'',
					IsSubPage:true
				},
				msg:'',
				
			}
		},
		components:{
			'v-headr':Headr,
		},
		methods: {
	      childClick () {
	        this.$emit('childByValue', this.IsSubPage)
	      }
	    },
	    mounted(){
	    	this.childClick()
	    }
	}
</script>


