<!--  -->
<template>
	<div id="YouZhiYouLiao">
        <v-headr :myname="myname"></v-headr>
		 <section class="aui-scrollView YouZhiYouLiao">  
                <div class="aui-card-list"> 
                    <div v-show="sosuo">
                        
                    

                    <div class="area-title"><h4><!-- <span class="more" @click="goMore('0564')">dd</span> --></h4></div>
                    <ul class="yy-mod">
                        <li @click="goToSerch('0550')" class="main_cat active"></li> 
                        <li @click="goToSerch('0564')" class="main_cat"></li>
                        <li @click="goToSerch('0359')" class="main_cat"></li>
                        <li @click="goToSerch('0352')" class="main_cat"></li>
                        <li @click="goToSerch('0360')" class="main_cat"></li>
                        <li @click="goToSerch('0333')" class="main_cat"></li> 
                    </ul>

                    <div class="area-title"><h4><!-- <span class="more" @click="goMore('0435')">yl</span> --></h4></div>
                    <ul class="yy-mod">
                        <li @click="goToSerch('0329')" class="main_cat active">24</li> 
                        <li @click="goToSerch('0353')" class="main_cat"></li>
                        <li @click="goToSerch('0362')" class="main_cat"></li>
                        <li @click="goToSerch('0327')" class="main_cat"></li>
                        
                    </ul>

                    </div>

 <div class="area-title"><h4><!-- <span class="more"></span> --></h4></div>
                   <div > <!-- v-show="liebiao" -->
                       <a href="javascript:void(0);"
                       class="aui-panel-cell"
                       v-for="(item,index) in list" > 

                        <div class="aui-panel-cell-bd">
                            <h4>{{item.title}}</h4>
                            <span>{{item.date}}</span>
                        </div> 
                        <div class="float-rigt">
                            <em class="jiage">{{item.price}}</em>
                        </div>
                    </a>
                   </div>
                     
                </div>
                

            </section>
	</div>
</template>

<script type="text/javascript">
    const youzhiyouliao =  sessionStorage.youzhi;
    const dataList_YoZhi = JSON.parse(youzhiyouliao);
    console.log("")
    console.dir(dataList_YoZhi)
    import Headr from '../../components/header.vue'
	export default {
		name:'YouZhiYouLiao',
		data(){
			return {
				msg:'',
                myname:{
                    name:'',
                    IsSubPage:true
                },
                list:null,
                sosuo:true,
                liebiao:false
			}
		},
        components:{
            'v-headr':Headr,
        },
		methods:{
            getUrlParam(key) {
      // 
                var url = window.location.search;
                // 
                var reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)");
                // 
                var result = url.substr(1).match(reg);
                //
                return result ? decodeURIComponent(result[2]) : null;
            },
			childClick(){
				this.$emit('childByValue',this.IsSubPage)
			},
            init_list(){
                console.log(""); 
                var isSear = this.getUrlParam("isSearch");
                var lisCity = this.getUrlParam("Cty");

                if(isSear!=null){
                    this.liebiao = true;
                    this.sosuo = false;

                    var tmpArr = new Array();
                    var list_1 = dataList_YoZhi.result[0].JinKouDaDou;
                     var list_2 = dataList_YoZhi.result[0].YouYongDaDou;
                     var list_3 = dataList_YoZhi.result[0].ShiYongDaDou;
                 for(let i in list_1){tmpArr.push(list_1[i])}
                    for(let j in list_2){tmpArr.push(list_2[j])}
                    for(let k in list_3){tmpArr.push(list_3[k])}   
                    this.getliebiao(tmpArr);

                }
            
            },
            goMore(){

            },
            goCity(key){
                this.$router.push({ name: 'YouZhiYouLiaoCont', query: { q: key }})
            },
            getliebiao(list_1){
                console.log(list_1)
                 this.list= list_1; 
            },
            goToSerch(kind){
                window.location.href="/JiShi/ZongHeChaXun?hotCityID="+kind;
            },
            goMore:function(q){  
                 // this.$router.push('/SanDaZhuLiang_Cont');
                 this.$router.push({ name: 'SanDaZhuLiang_Cont', query: { q: q }})
             },
		},
		mounted(){



		  	this.childClick();
            this.init_list();

		  }
	}
</script>


